.buttons {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fade {
    opacity: 0.5;
}

.button {
    text-align: center;
    font-size: large;
    color: rgb(75, 63, 63);
    width: 100px;
    border: 2px rgb(136, 122, 122) solid;
    border-radius: 100px;
    padding: 10px;
    background-color: rgb(210, 210, 210);
    margin: 30px;
}



.button:active {
    background-color: rgb(169, 169, 169);
}